  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

.wrap-for-action {
  display: flex;
  height: 50vh;
  width: 100%;
  align-items: center;
  justify-content: center;
}

body {
  position: relative;
  overflow-x: hidden;
  height: 100vh;
  margin: 0;
}

.loader-wrap {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
.loader-wrap.is-hidden {
  display: none;
}

.loader {
  position: absolute;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  animation: rotate 1s linear infinite;
  transform: translate(-50%, -50%);
}

.loader::before,
.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  inset: 0px;
  border-radius: 50%;
  border: 5px solid #FFF;
  animation: prixClipFix 2s linear infinite;
}

.loader::after {
  inset: 8px;
  transform: rotate3d(90, 90, 0, 180deg);
  border-color: #FF3D00;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
